*{
    box-sizing: border-box;
}

body {
    background-color: rgb(184, 204, 137);
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;
}

html, .container, body, #root {
    height: 100%;
    display:flex;
    justify-content: center;
}




.loader {
    width:100%;
    height:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight:300;
    font-size:30px;
}

.movies {
    display: flex;
    justify-content: space-between;
    align-items:center;
    flex-wrap: wrap;
    padding:30px;
    padding-top:70px;
    font-weight:300;
    width:80%;
}

