.movies .movie {
    width:45%;
    background-color:white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    padding:20px;
    padding-right:10px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
    max-width: 160px;
    width:100%;
    margin-right: 15px;
    position:relative;
    top:-50px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie__year,
.movie .movie__runtime,
.movie .movie__genres {
    opacity: 0.6;
}

.movie .movie__summary {
    opacity: 0.8;
}

.movie .movie__title,
.movie .movie__year,
.movie .movie__runtime {
    margin:0;
    font-weight:300;
}

.movie .movie__title {
    font-size:23px;
    font-weight:450;
    margin-bottom:5px;
}

.movie .movie__genres {
    list-style: none;
    margin:0;
    padding:0;
    display:flex;
    margin:5px 0;
}

.movie__genres li {
    margin-right:5px;
    font-size:14px;
}